.input {
  width: 100px;
  height: 30px;
  font-weight: bold;
  background-color: white;
  border: 3px solid darkgray;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
  outline: none;
}

.error {
  border: 3px solid red;
  color: red;
  background-color: rgb(233, 179, 179);
}
